import { Component, ViewChildren, ViewChild } from "@angular/core";
import { ChildenComponent } from "./children.component";

@Component({
    template : `
    <app-children></app-children><button (click)="tinhtong();">Add</button>
    `,
    selector : 'app-parent'
})

export class ParentComponent {

    @ViewChild  (ChildenComponent)

    myChildren : ChildenComponent;

    tinhtong() {
        this.myChildren.value ++;
    }

};