import { Component, Input, Output, EventEmitter } from "@angular/core";

@Component({
    template : `
    <button (click)="cong()">Add</button>
    <button (click)="tru()">Add</button>
    `,
    selector : 'app-con'
})

export class ConComponent {

    @Output() conEven = new EventEmitter<String>();
    cong() {
        this.conEven.emit('cong');
    }

    tru() {
        this.conEven.emit('tru');
    }
}