import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { CommonModule } from "@angular/common";
import { ContactsModule } from "./contact/contac.module";


import { DetailComponent } from './detail/detail.component';
import { PageNotPoundComponent } from './page-not-pound/page-not-pound.component';

const routes : Routes = [
  { path : 'detail/:id/:name' , component : DetailComponent },
  { path : '' , redirectTo : '/contact', pathMatch : 'full' },
  { path : '**', component : PageNotPoundComponent }
];

@NgModule({
  imports: [
    ContactsModule,
    RouterModule.forRoot(routes), 
    CommonModule
  ],
  declarations : [
    DetailComponent,
    PageNotPoundComponent
  ],
  exports: [RouterModule]
})
export class AppRoutingModule { }
