import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-subject',
  templateUrl: './subject.component.html',
  styleUrls: ['./subject.component.css']
})
export class SubjectComponent implements OnInit {

  isShow = false;
  tenMon = '';
  maMon = '';
  daHoc = false;
  status = 'xemTatCa';

  arrSubject = [
    {maMon: 'T1', tenMon: 'Angular', daHoc: true},
    {maMon: 'T2', tenMon: 'NoteJS', daHoc: false},
    {maMon: 'T3', tenMon: 'React', daHoc: false},
    {maMon: 'T4', tenMon: 'PHP', daHoc: true},
    {maMon: 'T5', tenMon: 'Java', daHoc: true}
  ];

  constructor() { }

  ngOnInit() {
  }

  themMonHoc() {
    
    this.arrSubject.push({maMon : this.maMon, tenMon : this.tenMon, daHoc : this.daHoc});
    this.maMon = '';
    this.tenMon = '';
    this.daHoc = false;
    this.isShow = false;
  }

  xoaDuLieu(maMonHoc : String) {
    let index = this.arrSubject.findIndex(monhoc => monhoc.maMon === maMonHoc);
    this.arrSubject.splice(index, 1);
  }

  checkXem(hoc : boolean) {
    const dkXemTatCa = this.status === 'xemTatCa';
    const dkXemDaHoc = this.status === 'xemDaHoc' && hoc;
    const dkXemChuaHoc = this.status === 'xemChuaHoc' && !hoc;
    return dkXemChuaHoc || dkXemDaHoc || dkXemTatCa;
  }
}
