import { Component } from "@angular/core";

@Component({
    template : `
    {{ value }}
    <app-con (conEven)="tinhToan($event);" ></app-con>
    `,
    selector : 'app-cha'
})

export class ChaComponent {

    value = 0;

    tinhToan(pheptinh : string) {
        
        this.value = pheptinh === 'cong' ? this.value + 1 : this.value - 1;
    }

}